body {
    font-family: "Poppins", sans-serif;
}

.custom-hr {
    background-color: white;
    width: 100%;
    height: 2px;
}
.custom-hr div {
    float: right;
    width: 80%;
    height: 100%;
    background: linear-gradient(to right, #1761e9, #c96ddd, #f45162);
}
.switch {
    width: 70px;
    position: fixed;
    bottom: 30px;
    left: 30px;
    z-index: 5;
}

.nav {
    width: 100%;
    display: flex;
    justify-content: center;
}

.shadow {
    background-image: url("./assets/back.jpg");
    filter: brightness(40%);
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-size: cover;
    z-index: -2;
}
